
import { defineComponent, toRefs, PropType } from "vue";
import { useRouter } from "vue-router";

import { formatImageUrl } from "@/utils/image";
import { CollectionAddFabric, CollectionFabrics } from "@/typings/collection";

const Component = defineComponent({
  name: "FabricCardEditable",
  props: {
    data: {
      type: Object as PropType<CollectionFabrics>,
      default: () => ({})
    }
  },
  setup(props, { emit }) {
    const router = useRouter();

    const { data } = toRefs(props);

    const onImageClick = async () => {
      await router.push({
        name: "FabricDetail",
        params: { id: data.value.fabric.id }
      });
    };

    const onPlusClick = () => {
      const params: CollectionAddFabric = {
        id: data.value.fabric.id,
        price: data.value.price
      };
      emit("add", params);
    };

    const onEditClick = () => {
      emit("edit", data.value);
    };

    const onDeleteConfirm = () => {
      emit("delete", data.value);
    };

    return {
      onImageClick,
      onPlusClick,
      onEditClick,
      onDeleteConfirm,
      formatImageUrl
    };
  }
});

export default Component;
