<template>
  <div id="fabric-card-editable-component">
    <div
      class="cover-image"
      :style="{
        backgroundImage: `url('${formatImageUrl(
          data.fabric.image_url
        )}@250-250')`
      }"
      @click="onImageClick"
    >
      <div class="top-console">
        <div
          v-permission="'collection_fabric_add'"
          class="button-wrapper"
          @click.stop="onPlusClick"
        >
          <font-awesome-icon
            class="icon"
            :icon="['fas', 'plus']"
          ></font-awesome-icon>
        </div>

        <div
          v-permission="'collection_price_update'"
          class="button-wrapper"
          @click.stop="onEditClick"
        >
          <font-awesome-icon
            class="icon"
            :icon="['fas', 'edit']"
          ></font-awesome-icon>
        </div>

        <div v-permission="'collection_delete'">
          <el-popconfirm
            confirmButtonText="Done"
            cancelButtonText="Cancel"
            confirmButtonType="danger"
            cancelButtonType="info"
            @confirm="onDeleteConfirm"
            hideIcon
            title="Are you sure you want to remove this fabric?"
          >
            <template #reference>
              <div class="button-wrapper">
                <font-awesome-icon
                  class="icon"
                  :icon="['fas', 'trash-alt']"
                ></font-awesome-icon>
              </div>
            </template>
          </el-popconfirm>
        </div>
      </div>
      <div class="view-container">
        <font-awesome-icon
          class="icon"
          :icon="['fas', 'eye']"
        ></font-awesome-icon>
        <p>0</p>
      </div>
      <div class="cover"></div>
    </div>
    <div class="content-container">
      <div class="content-wrapper">
        <h3 class="title">{{ data.fabric.name }}</h3>
      </div>
      <div class="price-group">
        <p class="price">$ {{ data.price }}</p>
      </div>
      <div class="hover-information-wrapper">
        <p v-if="data.fabric.art_no" class="description">{{ data.fabric.art_no }}</p>
        <p v-if="data.fabric.sap_art_no" class="description">{{ data.fabric.sap_art_no }}</p>
        <p v-if="data.fabric.real_composition" class="description">
          {{ data.fabric.real_composition }}
        </p>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, PropType } from "vue";
import { useRouter } from "vue-router";

import { formatImageUrl } from "@/utils/image";
import { CollectionAddFabric, CollectionFabrics } from "@/typings/collection";

const Component = defineComponent({
  name: "FabricCardEditable",
  props: {
    data: {
      type: Object as PropType<CollectionFabrics>,
      default: () => ({})
    }
  },
  setup(props, { emit }) {
    const router = useRouter();

    const { data } = toRefs(props);

    const onImageClick = async () => {
      await router.push({
        name: "FabricDetail",
        params: { id: data.value.fabric.id }
      });
    };

    const onPlusClick = () => {
      const params: CollectionAddFabric = {
        id: data.value.fabric.id,
        price: data.value.price
      };
      emit("add", params);
    };

    const onEditClick = () => {
      emit("edit", data.value);
    };

    const onDeleteConfirm = () => {
      emit("delete", data.value);
    };

    return {
      onImageClick,
      onPlusClick,
      onEditClick,
      onDeleteConfirm,
      formatImageUrl
    };
  }
});

export default Component;
</script>

