<template>
  <div id="collection-create-project-dialog-component">
    <el-dialog
      title="Initiate Project"
      v-model="visible"
      :width="step === 1 ? '80%' : '40%'"
      top="45px"
    >
      <!--Step 1-->
      <div class="step-1-container" v-if="step === 1">
        <el-row :gutter="60" v-if="detail">
          <el-col
            v-for="item in detail.collection_fabrics"
            :key="item.fabric.id"
            :span="6"
          >
            <FabricCardSelectable
              :data="item.fabric"
              :selected="form.start_fabric === item.fabric.id"
              @select="onSelect"
            ></FabricCardSelectable>
          </el-col>
        </el-row>
      </div>

      <!--Step 2-->
      <div class="step-2-container" v-if="step === 2">
        <el-row :gutter="30">
          <el-col :span="24">
            <p class="label">Name</p>
            <el-input v-model="form.name"> </el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Expected Finish Time</p>
            <el-date-picker
              v-model="form.expected_end_at"
              value-format="YYYY-MM-DD"
              placeholder="Select Date"
            >
            </el-date-picker>
          </el-col>
          <el-col :span="24">
            <p class="label">Price (Optional)</p>
            <el-input v-model="form.price" type="number"></el-input>
          </el-col>
          <el-col :span="24">
            <p class="label">Brand</p>
            <el-select v-model="form.brand">
              <el-option
                v-for="item in brandOptions"
                :key="item.id"
                :value="item.value"
                :label="item.label"
              ></el-option>
            </el-select>
          </el-col>
          <el-col :span="24">
            <div class="label">Season</div>
            <el-select v-model="form.season" multiple placeholder="Select...">
              <el-option
                v-for="item in seasonOptions"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-col>
          <el-col :span="24">
            <p class="label">Garment Factory (Optional)</p>
            <el-select v-model="form.garment_factory" clearable>
              <el-option
                v-for="item in garmentFactoryOptions"
                :key="item.id"
                :value="item.value"
                :label="item.label"
              ></el-option>
            </el-select>
          </el-col>
        </el-row>
      </div>
      <template #footer>
        <span class="dialog-footer">
          <el-button v-if="step === 1" type="info" @click="visible = false">
            Cancel
          </el-button>
          <el-button v-if="step === 2" type="info" @click="step = 1">
            Back
          </el-button>
          <el-button v-if="step === 1" type="success" @click="step = 2">
            Next
          </el-button>
          <el-button v-if="step === 2" type="success" @click="onConfirm">
            Done
          </el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive, computed } from "vue";
import { useStore } from "vuex";
import FabricCardSelectable from "@/components/FabricCardSelectable.vue";
import { GetCollection } from "@/typings/store";
import { OptionItem } from "@/typings/asideFilter";
import { useRouter } from "vue-router";
import { FabricCardSelectableEmit } from "@/typings/fabric";
import { ProjectCreateInputs } from "@/typings/project";
import { getAvaliableOptions, postCreateProject } from "@/apis/project";
import { ElNotification } from "element-plus";
import { seasonOptions } from "@/views/Collection/options";

const Component = defineComponent({
  name: "CreateDialog",
  components: {
    FabricCardSelectable
  },
  setup() {
    const router = useRouter();
    const store = useStore();

    const visible = ref(false);
    const step = ref(1);

    const brandOptions = ref<OptionItem[]>([]);
    const garmentFactoryOptions = ref<OptionItem[]>([]);
    const form = reactive<ProjectCreateInputs>({
      name: "",
      expected_end_at: null,
      price: 0,
      start_fabric: 0,
      brand: null,
      season: [],
      garment_factory: null,
      collection: null
    });

    const detail = computed(() => store.state.collection.detail);

    const init = async (id: number) => {
      await store.dispatch(GetCollection, id);
      const brandResponse = await getAvaliableOptions("brand");
      const garmentFactoryResponse = await getAvaliableOptions(
        "garment_factory"
      );
      brandOptions.value = brandResponse.data.brands.map(
        (it: { name: string; id: number }): OptionItem => ({
          label: it.name,
          value: it.id,
          checked: false
        })
      );
      garmentFactoryOptions.value = garmentFactoryResponse.data.brands.map(
        (it: { name: string; id: number }): OptionItem => ({
          label: it.name,
          value: it.id,
          checked: false
        })
      );
    };

    const open = async (_id: number) => {
      step.value = 1;
      form.collection = _id;
      visible.value = true;
      await init(_id);
    };

    const onConfirm = async () => {
      if (!form.name || !form.expected_end_at || !form.brand) {
        return ElNotification({
          title: "Warning",
          type: "warning",
          message: `Should complete the form of Initiate Project.`
        });
      }
      const _form: ProjectCreateInputs = {
        name: form.name,
        expected_end_at: form.expected_end_at,
        start_fabric: form.start_fabric,
        brand: form.brand,
        season: form.season,
        garment_factory: form.garment_factory
      };
      if (form.price) _form.price = form.price;
      const { data } = await postCreateProject(_form);
      visible.value = false;
      await router.push({ name: "ProjectDetail", params: { id: data.id } });
    };

    const onSelect = (data: FabricCardSelectableEmit) => {
      if (data.selected) return;
      form.start_fabric = data.id;
    };

    return {
      visible,
      step,
      form,
      detail,
      brandOptions,
      seasonOptions,
      garmentFactoryOptions,

      open,
      onConfirm,
      onSelect
    };
  }
});

export default Component;
</script>

